<template>
  <div class="content">
    <div class="header">
      <div class="head">
        <router-link to="/account" class="back"></router-link>
        {{ $t('lang.change_login_pw') }}
      </div>
    </div>
    <div class="resetpaypwd_wrap">
      <h5 class="set_tips" :lang="$i18n.locale">
        {{ $t('lang.setting_login_pw') }}
      </h5>
      <form class="form" :lang="$i18n.locale" @submit.prevent="handleSubmit">
        <label class="item">
          <span class="info">{{ $t('lang.old_pw') }} </span>
          <div class="item_new">
            <input
              :type="oldpwd_type"
              class="inp oldpwd"
              v-model.trim="passwd"
              :placeholder="$t('lang.input_old_pw')"
            />
            <span
              class="right_icon"
              :class="oldpwd_type == 'password' ? '' : 'right_icon_open'"
              @click="show_passwd"
            ></span>
          </div>
        </label>
        <label class="item">
          <span class="info">{{ $t('lang.new_pw') }} </span>
          <div class="item_new">
            <input
              :type="newpwd_type"
              class="inp newpwd"
              v-model.trim="npasswd"
              :placeholder="$t('lang.input_new_pw')"
            />
            <span
              class="right_icon"
              :class="newpwd_type == 'password' ? '' : 'right_icon_open'"
              @click="show_npasswd"
            ></span>
          </div>
        </label>
        <label class="item">
          <span class="info">{{ $t('lang.confirm_new_pw') }} </span>
          <div class="item_new">
            <input
              :type="confirm_newpwd_type"
              class="inp confirm_newpwd"
              v-model.trim="snpasswd"
              :placeholder="$t('lang.new_pw_match')"
            />
            <span
              class="right_icon"
              :class="
                confirm_newpwd_type == 'password' ? '' : 'right_icon_open'
              "
              @click="show_snpasswd"
            ></span>
          </div>
        </label>
        <!--                <div class="forget_box">-->
        <!--                    <router-link to="/forgetpwd" class="forget"><span class="icon">&nbsp;?</span><span class="info"></span></router-link>-->
        <!--                </div>-->
      </form>
    </div>
    <button
      type="submit"
      class="sbtn basic_btn_v2"
      :class="passwd != '' && npasswd != '' && snpasswd != '' ? '' : 'no_touch'"
      @click="handleSubmit"
    >
      {{ $t('lang.confirm_edit') }}
    </button>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';

export default {
  name: 'index',
  components: {},
  data() {
    return {
      passwd: '',
      npasswd: '',
      snpasswd: '',
      oldpwd_type: 'password',
      newpwd_type: 'password',
      confirm_newpwd_type: 'password',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    //this.start();
  },
  methods: {
    show_passwd() {
      if (this.oldpwd_type == 'password') {
        this.oldpwd_type = 'text';
      } else {
        this.oldpwd_type = 'password';
      }
    },
    show_npasswd() {
      if (this.newpwd_type == 'password') {
        this.newpwd_type = 'text';
      } else {
        this.newpwd_type = 'password';
      }
    },
    show_snpasswd() {
      if (this.confirm_newpwd_type == 'password') {
        this.confirm_newpwd_type = 'text';
      } else {
        this.confirm_newpwd_type = 'password';
      }
    },
    handleSubmit() {
      if (!this.passwd) {
        this.$notify({
          message: this.$t('lang.input_pw'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.npasswd) {
        this.$notify({
          message: this.$t('lang.input_new_pw'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.snpasswd) {
        this.$notify({
          message: this.$t('lang.input_confirm_pay_pw'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (this.npasswd !== this.snpasswd) {
        this.$notify({
          message: this.$t('lang.new_pw_match'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      Fetch('/user/info/repasswd', {
        passwd: this.passwd,
        npasswd: this.npasswd,
      }).then(() => {
        this.$notify({
          background: '#EAF1FF',
          color: '#3292FF',
          message: this.$t('lang.edit_success'),
        });
        this.$router.back();
      });
    },
  },
};
</script>

<style lang="less" scoped>
@import './css/set.less';
.set_tips:lang(yn) {
  font-size: 0.48rem;
}
.form:lang(yn) {
  label {
    .info {
      width: 100% !important;
      font-size: 0.44rem !important;
    }
    div {
      input:-ms-input-placeholder {
        font-size: 0.2375rem;
      } /* Internet Explorer 10+ */

      input::-webkit-input-placeholder {
        font-size: 0.2375rem;
      } /* WebKit browsers */

      input::-moz-placeholder {
        font-size: 0.2375rem;
      } /* Mozilla Firefox 4 to 18 */

      input:-moz-placeholder {
        font-size: 0.2375rem;
      } /* Mozilla Firefox 19+ */
    }
  }
}
</style>
