import Vue from 'vue';
import axios from 'axios';
import qs from 'qs';
import { Toast } from 'vant';
import router from '@/router';
//import {Notify} from 'vant'
import { Dialog } from 'vant';

import VueI18n from 'vue-i18n';
Vue.use(VueI18n);
const i18n = new VueI18n({
  locale: localStorage.getItem('locale') || 'yn', //  //this.$i18n.locale // locale
  messages: {
    cn: require('../../VueI18n/cn'),
    //en: require('../../VueI18n/en'),
    yn: require('../../VueI18n/yn'),
  },
});

let loadingCount = 0;

export const showLoading = () => {
  if (loadingCount <= 0) {
    Toast.loading({
      // mask: true,
      icon: 'like-my',
      duration: 20000,
    });
  }
  loadingCount++;
};

export const hideLoading = () => {
  loadingCount--;
  if (loadingCount <= 0) {
    Toast.clear();
    loadingCount = 0;
  }
};

let ssid = localStorage.getItem('ssid');

export default async (url, data, opt, isLoad = true) => {
  if (isLoad) {
    showLoading();
  }

  const set = { method: 'post', ...opt };
  const nowTime = Date.parse(new Date()) / 1000;
  var expireTime = parseInt(localStorage.getItem('expire_time'));
  const resetTime = parseInt(localStorage.getItem('reset_time'));

  if (!ssid || !expireTime || !resetTime || resetTime < nowTime) {
    await fetch('/api/api/config')
      .then(res => {
        return res.json();
      })
      .then(res => {
        ssid = res.data.ssid;
        localStorage.setItem('ssid', ssid);
        localStorage.setItem(
          'expire_time',
          parseInt(res.data.ssid_expire_time)
        );
        expireTime = parseInt(res.data.ssid_expire_time);
      })
      .catch(() => {
        Dialog.alert({
          title: i18n.t('lang.notice'),
          message: i18n.t('lang.net_error'),
        });
      });
  }
  localStorage.setItem('reset_time', nowTime + expireTime);
  // console.log('************>', data);
  return (set.method === 'post'
    ? axios.post(
        url,
        { ...data, ssid },
        {
          headers: { language: localStorage.getItem('locale') },
        }
      )
    : axios.get(
        url +
          '?' +
          qs.stringify({
            ...data,
            ssid,
          })
      )
  )
    .then(r => {
      if (isLoad) {
        hideLoading();
      }

      if (r.status === 200 && r.data.code) {
        return r.data;
      }

      throw { msg: i18n.t('lang.net_err_retry') };
    })
    .then(r => {
      if (r.code === 200) {
        return r;
      } else {
        if (!r.code) {
          throw { msg: i18n.t('lang.net_err_retry') };
        }

        if (r.code === 406) {
          router.push('/firstsetpaypwd');
        }

        if (r.code === 403 || r.code === 401) {
          if (r.code === 401) {
            localStorage.setItem('reset_time', 0);
          }

          const current = { ...router.currentRoute };
          if (
            current &&
            current.path !== '/' &&
            !['/', '/reg', '/forgetpwd'].includes(current.path)
          ) {
            if (current.path !== '/login') {
              router.replace('/login??redirect=' + current.path);
            }
          }
        }
      }

      throw r;
    })
    .catch(r => {
      if (isLoad) {
        hideLoading();
      }

      var message = i18n.t('lang.net_err_retry');
      if (r.msg) {
        message = r.msg;
      }

      if (r.msg == i18n.t('lang.no_address')) {
        Dialog.alert({
          title: i18n.t('lang.notice'),
          message: message,
          showCancelButton: true,
          confirmButtonText: i18n.t('lang.go_add'),
        })
          .then(() => {
            location.href = '#address';
            location.reload();
          })
          .catch(() => {});
        throw '';
      }

      if (r.code === 404 || r.code === 410) {
        let msg = '';
        if (!r.dev_show_msg) {
          msg = r.msg;
        } else {
          msg = r.dev_show_msg;
        }
        Dialog.alert({
          title: i18n.t('lang.notice'),
          message: msg,
          showCancelButton: true,
          confirmButtonText: i18n.t('lang.submit'),
          cancelButtonText: i18n.t('lang.cancel'),
        })
          .then(() => {})
          .catch(() => {});
        throw '';
      }

      if (isLoad) {
        Dialog.alert({
          title: i18n.t('lang.notice'),
          message: message,
          confirmButtonText: i18n.t('lang.confirm'),
        }).then(() => {
          if (r.code === 405) {
            const current = { ...router.currentRoute };
            router.push('/auth?redirect=' + current.path);
          }
        });
      }
      //Notify(message);

      throw '';
    });
};
